<template>
  <div class="mt-10">
    <v-layout wrap>
    <v-flex sm12 md8 offset-md2>
      <v-layout align-center justify-space-between>
        <span class="text-h4">
          
        </span>
      </v-layout>
      <v-divider class="mt-7 mb-3"/>
      <v-data-table
        v-model="selected"
        show-select
        item-key="id"
        :headers="headers"
        :items="clients"
        :search="search"
        class="elevation-1"
        :footer-props="{
          showFirstLastPage: true,
          'items-per-page-text': ''
        }"
      >
      <template v-slot:top>
        <v-toolbar
            flat
        >
          <v-col cols="12" sm="6" md="4">
          <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label=""
              single-line
              hide-details
          ></v-text-field>
          </v-col>
          <v-spacer></v-spacer>
          <v-dialog
              v-model="dialog"
              max-width="500px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                  color="info"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
              >
                
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col
                        cols="12"
                        sm="6"
                        md="7"
                    >
                      <v-text-field
                          v-model="editedItem.name"
                          label=""
                      ></v-text-field>
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="5"
                    >
                      <v-text-field
                          v-model="editedItem.manager"
                          label=""
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col
                        cols="12"
                        sm="6"
                        md="5"
                    >
                      <v-text-field
                          v-model="editedItem.tel"
                          label=""
                      ></v-text-field>
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="7"
                    >
                      <v-text-field
                          v-model="editedItem.address"
                          label=""
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                    color="blue darken-1"
                    text
                    @click="close"
                >
                  
                </v-btn>
                <v-btn
                    color="blue darken-1"
                    text
                    @click="save"
                >
                  
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-center justify-center">?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"></v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"></v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
            small
            @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
      <template v-slot:no-data>
        <span></span>
      </template>
        <template v-slot:footer.prepend>
          <download-csv
            :data = "selected">
          <v-btn plain color="primary">
            <v-icon>
              mdi-download
            </v-icon>
            CSV
          </v-btn>
          </download-csv>
        </template>
      </v-data-table>
    </v-flex>
    </v-layout>
  </div>
</template>

<script>
import ClientService from "../services/client.service";
import JsonCsv from 'vue-json-csv';
export default {
  name: "Client",
  components: {
    'downloadCsv': JsonCsv
  },
  data: () => ({
    json_data: [
      {
        'name': 'Tony Pea',
        'city': 'New York',
        'country': 'United States',
        'birthdate': '1978-03-15',
        'phone': {
          'mobile': '1-541-754-3010',
          'landline': '(541) 754-3010'
        }
      },
      {
        'name': 'Thessaloniki',
        'city': 'Athens',
        'country': 'Greece',
        'birthdate': '1987-11-23',
        'phone': {
          'mobile': '+1 855 275 5071',
          'landline': '(2741) 2621-244'
        }
      }
    ],
    dialog: false,
    dialogDelete: false,
    search: '',
    selected: [],
    headers: [
      {
        text: '',
        align: 'start',
        value: 'name',
      },
      { text: '', value: 'id' },
      { text: '', value: 'manager' },
      { text: '', value: 'tel' },
      { text: '', value: 'address' },
      { text: '', value: 'actions', sortable: false },
    ],
    clients: [],
    editedIndex: -1,
    editedItem: {
      id: '',
      name: '',
      manager: '',
      tel: '',
      address: ''
    },
    defaultItem: {
      id: '',
      name: '',
      manager: '',
      tel: '',
      address: ''
    },
  }),

  computed: {
    formTitle () {
      return this.editedIndex === -1 ? '' : ''
    },
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
  },

  created () {
    this.retrieveClients()
  },

  methods: {
    retrieveClients () {
      ClientService.getAll()
        .then(response => {
          if(response.status === 200) {
            this.clients = response.data;
          }
        });
    },

    editItem (item) {
      this.editedIndex = this.clients.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem (item) {
      this.editedIndex = this.clients.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm () {
      ClientService.delete(this.editedItem.id)
        .then(response => {
          if(response.status === 200) {
            this.clients.splice(this.editedIndex, 1)
            this.closeDelete()
          }
        })
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {
      if (this.editedIndex > -1) {
        ClientService.update(this.editedItem);
        Object.assign(this.clients[this.editedIndex], this.editedItem)
      } else {
        ClientService.create(this.editedItem)
            .then(response => {
              this.clients.push(response.data);
            })
      }
      this.close()
    },
  },
}
</script>

